

.search-body{
        width: 100vw;
        height: 70%;
        position: absolute;
        top: 26%;
        left: 0;

        h2{
                font-family: 'Work Sans', sans-serif;
                font-size: 2.8rem;
                margin: 0 0 0 10%;
                color: #2A374A;
            }
        
            p{
                font-family: 'Work Sans', sans-serif;
                font-size: 1.2rem;
                margin: 1% 0 0 10%;
                color: #2A374A;
                opacity: 0.6;
            }
        
            .card-container{
                width: 80%;
                height: 100%;
                position: absolute;
                left: 15%;
                top: 5%;
                display: flex;
                flex-direction: row;
                justify-content: flex-start;
                flex-wrap: wrap;
            
        
        
        
                .card{
                   
                    order: 1;
                    width: 20rem;
                    height: 25rem;
                    background-color: #F4F6FA;
                    border-radius: 0.5rem;
                    margin: 10% 6% 0 0;
                    box-shadow: rgba(0, 0, 0, 0.25) 0px 14px 28px, rgba(0, 0, 0, 0.22) 0px 10px 10px;
                    cursor: pointer;
                    position: relative;
        
                    img{
                        max-width: 100%;
                        height: 100%;
                        border-radius: 0.5rem;
                        position: absolute;
                        top: 0;
                        left: 0;
                        z-index: 0;
                    }
        
                    p{
                        background: rgba(42, 55, 74, 0.35);
                        border-radius: 0.2rem;
                        box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
                        backdrop-filter: blur(15.8px);
                        -webkit-backdrop-filter: blur(15.8px);
                        padding: 2% 2%;
                        z-index: 5;
                        color: white;
                        font-weight: 800;
                        opacity: 1;
                        position: absolute;
                        top: 80%;
                        left: 0;
                    }

                    &:hover{
                        opacity: 0.3;
                     }
                 
                }
        }
    
}